<template>
  <v-container fluid>
    <v-card flat>
      <v-card-title>
        <v-spacer/>
        <v-text-field
          dense
          v-model="search"
          append-icon="mdi-search"
          label="Tm kim"
        />
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="students"
        :search="search"
      >
        <template
          v-slot:top
        >
          <v-toolbar flat color="white">
            <v-card-title>
              Danh sch sinh vin
            </v-card-title>
            <v-spacer/>
          </v-toolbar>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      dialog: false,
      search: '',
      headers: [
        {
          text: 'M sinh vin',
          value: 'code',
          filterable: true,
        },
        {
          text: 'H v tn',
          value: 'fullName',
          filterable: true,
        },
        {
          text: 'Email',
          value: 'email',
          filterable: false,
        },
        {
          text: 'S in thoi',
          value: 'phoneNumber',
          filterable: false,
        },
        {
          text: 'Ngnh',
          value: 'sector.name',
          filterable: false,
        },
      ],
      editedItem: {},
    };
  },
  created() {
    this.$store.dispatch('loadStudents');
    this.$store.dispatch('loadSectors');
  },
  computed: {
    ...mapGetters(['students', 'sectors']),
    formTitle() {
      return this.editedItem._id ? 'Sa thng tin sinh vin' : 'Thm thng tin sinh vin';
    },
  },
  methods: {
    editItem(item) {
      this.editedItem = {
        _id: item._id,
        code: item.code,
        email: item.email,
        fullName: item.fullName,
        dateOfBirth: item.dateOfBirth,
        phoneNumber: item.phoneNumber,
        sector: item.sector._id,
      };
      this.dialog = true;
    },
    deleteItem(item) {
      this.$store.dispatch('deleteStudent', item);
    },
    close() {
      setTimeout(() => {
        this.editedItem = {
        };
      }, 300);
      this.dialog = false;
    },
    save() {
      if (this.editedItem._id) {
        this.$store.dispatch('updateStudent', this.editedItem);
      } else {
        this.$store.dispatch('createStudent', this.editedItem);
      }
      this.editedItem = {
      };
      this.dialog = false;
    },
  },
};
</script>
